import handleActions from '../utils/handleActions'
import {
	SET_USER,
	SetUserAction,
	UNSET_USER,
	SET_USER_LOADING,
	SET_DEFAULT_ADDRESS,
	SetDefaultAddressAction,
	SET_DEFAULT_TIME,
	SetDefaultTimeAction,
} from '../actions/user'
import { IAddress } from './addresses'

export interface IUserState {
	id: string
	email: string
	token: string
	loggedIn: boolean
	loading: boolean
	defaultAddress: null | IAddress
	defaultTime: null | string
}

const persistentState: Partial<IUserState> = localStorage.userState && JSON.parse(localStorage.userState)

const emptyState: IUserState = {
	id: '',
	email: '',
	token: '',
	loggedIn: false,
	loading: false,
	defaultAddress: null,
	defaultTime: null,
}
const initialState: IUserState = {
	...emptyState,
	...persistentState,
}

function persistUser(state: IUserState | null) {
	localStorage.userState = JSON.stringify(state)
}

// tslint:disable:function-name
export default handleActions(
	{
		[SET_USER](state, { payload }: SetUserAction) {
			const newState: IUserState = {
				...state,
				id: payload.id,
				email: payload.id,
				token: payload.token,
				loggedIn: true,
			}
			persistUser(newState)
			return {
				...newState,
				loading: false,
			}
		},
		[UNSET_USER]() {
			persistUser(null)
			return emptyState
		},
		[SET_USER_LOADING](state) {
			return {
				...state,
				loading: true,
			}
		},
		[SET_DEFAULT_ADDRESS](state, { payload }: SetDefaultAddressAction) {
			const newState = {
				...state,
				defaultAddress: payload,
			}
			persistUser(newState)
			return newState
		},
		[SET_DEFAULT_TIME](state, { payload }: SetDefaultTimeAction) {
			const newState = {
				...state,
				defaultTime: payload,
			}
			persistUser(newState)
			return newState
		},
	},
	initialState,
)



