import React from 'react'
import translate from '../../utils/translate'

export interface IAddressEditorProps {}

export interface IAddressEditorActionProps {
	submitAddress: (title: string, address: string) => void
}

interface IState {
	title: string
	address: string
}

export default class AddressEditor extends React.Component<
	IAddressEditorProps & IAddressEditorActionProps,
	IState
> {
	state: IState = {
		title: '',
		address: '',
	}

	render() {
		const { title, address } = this.state

		return (
			<section style={{ textAlign: 'center' }}>
				<h3>Edit address</h3>
				<form onSubmit={this.onSubmit.bind(this)}>
					<label>
						{translate('addresses.addressname')}:
						<br />
						<input
							type="text"
							value={title}
							onChange={this.onTitleChange.bind(this)}
							placeholder={translate('addresses.addressname.placeholder')}
						/>
					</label>
					<br />
					<br />
					<label>
						{translate('addresses.addressloc')}:
						<br />
						<textarea
							value={address}
							onChange={this.onAddressChange.bind(this)}
							placeholder={translate('addresses.addressloc.placeholder')}
						/>
					</label>
					<br />
					<br />
					<button type="submit">{translate('addresses.save')}</button>
					<br />
					<br />
				</form>
			</section>
		)
	}

	private onTitleChange(event: React.FormEvent<HTMLInputElement>) {
		this.setState({ title: event.currentTarget.value })
	}

	private onAddressChange(event: React.FormEvent<HTMLInputElement>) {
		this.setState({ address: event.currentTarget.value })
	}

	private onSubmit(event: React.FormEvent<HTMLFormElement>) {
		event.preventDefault()

		const { title, address } = this.state
		const { submitAddress } = this.props

		submitAddress(title, address)
	}
}



