import { connect } from 'react-redux'
import { logout } from '../actions/user'
import Settings, {
	ISettingsActionProps,
} from '../components/Settings'
import { IState } from '../reducers'

const mapStateToProps = (state: IState) => ({ defaultAddress: state.user.defaultAddress })
const mapDispatchToProps: ISettingsActionProps = { logout }

export default connect(
	mapStateToProps,
	mapDispatchToProps,
)(Settings)



