import { connect } from 'react-redux'
import { submitAddress } from '../actions/addresses'
import Addresses, {
	IAddressEditorProps,
	IAddressEditorActionProps,
} from '../components/AddressEditor'

const mapStateToProps = () => ({})
const mapDispatchToProps = { submitAddress }

export default connect<IAddressEditorProps, IAddressEditorActionProps>(
	mapStateToProps,
	mapDispatchToProps,
)(Addresses)



