import axios from 'axios'
import { Store } from 'react-redux'
import { IAddresses } from '../actions/addresses'
import { IProducts } from '../actions/products'
import formatProductPayload from './formatProductPayload'
import formatPromotionPayload from './formatPromotionPayload'
import { IPromotions } from '../actions/promotions'

class Api {
	private axios = axios.create({
		baseURL: 'https://cors-anywhere.herokuapp.com/https://api.beta.goodlok.cz',
	})

	private token: string = ''

	// @TODO: any
	public setStore(store: Store<any>) {
		const refresh = () => {
			const state = store.getState()
			const { token } = state.user
			if (token !== this.token) {
				if (token) {
					this.setAuthorizationToken(token)
				} else {
					this.unsetAuthorizationToken()
				}
			}
		}
		refresh()
		store.subscribe(refresh)
	}

	public async loginWithEmailPassword(email: string, password: string) {
		return this.axios
			.post('signin', {
				email,
				password,
			})
			.then((response) => {
				const { id, email, token } = response.data
				return {
					id,
					email,
					token,
				}
			})
	}

	public async requestAddresses() {
		return this.axios.get('user/addresses').then((response) => {
			const addresses: IAddresses = {}
			response.data.forEach((address: any) => {
				addresses[address.id] = {
					address: address.raw,
					id: address.id,
					title: address.title,
				}
			})
			return addresses
		})
	}

	public async addAddress(title: string, address: string) {
		return this.axios
			.post('user/address', { title, raw: address })
			.then((response) => {
				return {
					address,
					title,
					id: response.data.id,
				}
			})
	}

	public async removeAddress(id: string) {
		return this.axios.delete(`user/address/${id}`).then(() => {
			return null
		})
	}

	public async requestProducts() {
		return this.axios.get('products/offer').then((response) => {
			const products: IProducts = {}
			response.data.forEach((product: any) => {
				products[product.id] = formatProductPayload(product)
			})
			return products
		})
	}

	public async requestPromotions() {
		return this.axios.get('promotions').then((response) => {
			const promotions: IPromotions = {}
			response.data.forEach((promotion: any) => {
				promotions[promotion.id] = formatPromotionPayload(promotion)
			})
			return promotions
		})
	}

	private setAuthorizationToken(token: string) {
		this.axios.defaults.headers.common['Authorization'] = `Bearer ${token}`
	}

	private unsetAuthorizationToken() {
		delete this.axios.defaults.headers.common['Authorization']
	}
}

export default new Api()



