import React from 'react'
import styles from './styles.sass'
import SelectStrip, { ISelectStripItem } from '../SelectStrip'
import { IAddresses } from '../../actions/addresses'
import nl2br from '../../utils/nl2br'
import { IAddress } from '../../reducers/addresses'
import BasketTuner from '../../containers/BasketTunerContainer'
import { IBasketItems } from '../../actions/basket'
import Popup from '../Popup'

export interface IOrderRecapitulationOwnProps {
	isActive: boolean
	onClose: () => void
}
export interface IOrderRecapitulationProps {
	addresses: IAddresses
	items: IBasketItems
	selectedAddress: IAddress | null
	selectedTime: string | null
}

export interface IOrderRecapitulationActionProps {
	requestAddresses: () => void
	setDefaultAddress: (address: IAddress) => void
	setDefaultTime: (address: string) => void
}

interface IProps extends IOrderRecapitulationOwnProps, IOrderRecapitulationProps, IOrderRecapitulationActionProps {}

export default class OrderRecapitulation extends React.Component<
	IProps
> {

	componentDidMount() {
		this.props.requestAddresses()
	}

	componentWillReceiveProps(nextProps: IProps) {
		const itemsCountBefore = Object.keys(this.props.items).length
		const itemsCountAfter = Object.keys(nextProps.items).length
		if (itemsCountBefore !== itemsCountAfter && itemsCountAfter === 0) {
			nextProps.onClose()
		}
	}

	render() {
		const { isActive, onClose } = this.props

		return (
			<Popup
				isActive={isActive}
				onClose={onClose}
				bottomBar={(
					<React.Fragment>
						Order <i className="fas fa-arrow-right"></i>
					</React.Fragment>
				)}
			>
				<div className={styles.wrapper}>
					<div className={styles.products}>
						<BasketTuner />
					</div>
					<div className={styles.deliveryDetails}>
						<div className={styles.deliveryDetailsTitle}>Where?</div>
						<SelectStrip items={this.getWhereItems()} />
						<div className={styles.deliveryDetailsDivider} />
						<div className={styles.deliveryDetailsTitle}>When?</div>
						<SelectStrip items={this.getWhenItems()} />
					</div>
				</div>
			</Popup>
		)
	}

	private getWhereItems(): ISelectStripItem[] {
		const { setDefaultAddress, selectedAddress } = this.props

		const addItem: ISelectStripItem = {
			active: false,
			note: <React.Fragment><i className="fas fa-plus"></i> add address</React.Fragment>,
			onClick: () => console.log('@TODO: Add new address'),
		}
		return Object.values(this.props.addresses).map((address): ISelectStripItem => ({
			active: selectedAddress && selectedAddress.id === address.id,
			title: address.title,
			note: nl2br(address.address),
			onClick: () => setDefaultAddress(address),
		})).concat(addItem)
	}

	private getWhenItems(): ISelectStripItem[] {
		const { setDefaultTime, selectedTime } = this.props
		const times = ['12:00', '14:00', 'Tomorrow 10:00']

		const customTime: ISelectStripItem = {
			active: false,
			title: <React.Fragment><i className="far fa-edit"></i> custom time</React.Fragment>,
			onClick: () => console.log('@TODO: Custom time'),
		}

		return times.map((time): ISelectStripItem => ({
			active: time === selectedTime,
			title: time,
			onClick: () => setDefaultTime(time),
		})).concat(customTime)
	}
}



