import React from 'react'
import styles from './styles.sass'
import translate from '../../utils/translate'
import { IAddress } from '../../reducers/addresses'
import Addresses from '../../containers/AddressesContainer'

export interface ISettingsProps {
	defaultAddress: IAddress | null
}

export interface ISettingsActionProps {
	logout: () => void
}

enum SettingsListItem {
	Addresses,
	Payments,
	Repeating,
}

interface IState {
	activeListItem: SettingsListItem | null
}

export default class Settings extends React.Component<
	ISettingsProps & ISettingsActionProps, IState
> {
	state: IState = {
		activeListItem: null,
	}

	render() {
		const { logout, defaultAddress } = this.props
		const { activeListItem } = this.state

		const listItems = [
			{
				type: SettingsListItem.Addresses,
				title: translate('menu.myAddresses.title'),
				note: defaultAddress ? defaultAddress.title : translate('menu.myAddresses.desc'),
			},
			{
				type: SettingsListItem.Payments,
				title: translate('menu.payments.title'),
				note: translate('menu.payments.desc'),
			},
			{
				type: SettingsListItem.Repeating,
				title: translate('menu.repeating.title'),
				note: translate('menu.repeating.desc'),
			},
		]

		return (
			<section className={styles.wrapper}>
				<div className={styles.list}>
					{listItems.map((item) => (
						<div className={styles.listItem} key={item.type}>
							<div className={styles.listTitle}>
								{item.title}
							</div>
							<p className={styles.listNote}>
								{item.note}
							</p>
							<div className={styles.listArrow}>
								<i className="fas fa-chevron-right"></i>
							</div>
							<button
								role="button"
								aria-label="Open"
								className={styles.listAction}
								onClick={this.setActiveListItem(item.type)}
							/>
						</div>
					))}
				</div>

				<div className={styles.other}>
					<button className={styles.logout} role="button" onClick={() => logout()}>
						{translate('menu.logout')}
					</button>
					<a
						className={styles.social}
						href="https://www.messenger.com/t/goodloky"
						target="_blank"
						rel="noopener"
					>
						Messenger
					</a>
					<a
						className={styles.social}
						href="https://www.instagram.com/goodlokcz/"
						target="_blank"
						rel="noopener"
					>
						Instagram
					</a>
				</div>

				<Addresses
					isActive={activeListItem === SettingsListItem.Addresses}
					onClose={this.closeListItem}
				/>
			</section>
		)
	}

	private closeListItem = () => {
		this.setState({ activeListItem: null })
	}

	private setActiveListItem = (type: SettingsListItem) => () => {
		this.setState({ activeListItem: type })
	}
}



