import React from 'react'
import styles from './styles.sass'
import { IAddresses } from '../../actions/addresses'
import AddressEditor from '../../containers/AddressEditorContainer'
import Popup from '../Popup'
import nl2br from '../../utils/nl2br'
import { IAddress } from '../../reducers/addresses'
import cn from 'classnames'
import translate from '../../utils/translate'

export interface IAddressesOwnProps {
	isActive: boolean
	onClose: () => void
}

export interface IAddressesProps {
	addresses: IAddresses
	defaultAddress: IAddress | null
}

export interface IAddressesActionProps {
	requestAddresses: () => void
	removeAddress: (address: IAddress) => void
	setDefaultAddress: (address: IAddress) => void
}

export default class Addresses extends React.Component<
	IAddressesOwnProps & IAddressesProps & IAddressesActionProps
> {
	componentWillMount() {
		this.props.requestAddresses()
	}

	render() {
		const { addresses, isActive, onClose, defaultAddress, setDefaultAddress } = this.props

		return (
			<Popup isActive={isActive} onClose={onClose}>
				<div className={styles.wrapper}>
					<AddressEditor />
					<div className={styles.list}>
						{Object.values(addresses).map((address) => (
							<section
								className={cn(
									styles.item,
									defaultAddress && defaultAddress.id === address.id && styles.viewActive,
								)}
								key={address.id}
							>
								<h1 className={styles.title}>{address.title}</h1>
								<p className={styles.address}>{nl2br(address.address)}</p>
								<div className={styles.actions}>
									<button
										className={styles.action}
										type="button"
										aria-label="Edit"
										onClick={() => alert('@TODO: Not implemented')}
									>
									<i className="far fa-edit"></i>
									</button>
									<button
										className={styles.action}
										type="button"
										aria-label={translate('generic.delete')}
										onClick={this.remove(address)}
									>
										<i className="fas fa-trash"></i>
									</button>
								</div>
								<button
									className={styles.setDefault}
									role="button"
									aria-label="Set as default"
									onClick={() => setDefaultAddress(address)}
								/>
							</section>
						))}
					</div>
					<button
						role="button"
						className={styles.add}
					>
						Add address <i className="fas fa-plus"></i>
					</button>
				</div>
			</Popup>
		)
	}

	private remove = (address: IAddress) => () => {
		if (confirm(`Do you really want to remove "${address.title}"`)) {
			this.props.removeAddress(address)
		}
	}
}



